<template>
    <div>
        <van-form @submit="onSubmit">
            <van-field
                v-model="username"
                name="username"
                label="用户名"
                placeholder="用户名"
                :rules="[{ required: true, message: '请填写用户名' },{ validator: nameVal, message: '请输入4-16位字母开头用户名' }]"
            />
            <van-field
                v-model="password"
                type="password"
                name="password"
                label="密码"
                placeholder="密码"
                :rules="[{ required: true, message: '请填写密码' },{ validator: passwordVal, message: '请输入最少6位，包括至少1个大写字母，1个小写字母，1个数字，1个特殊字符' }]"
            />
            <van-field
                v-model="email"
                name="email"
                label="邮箱"
                placeholder="邮箱"
                :rules="[{ required: true, message: '请填写邮箱地址' },{ validator: mailVal, message: '请输入正确邮箱' }]"
            
            >
            <template #button>
                <van-button :disabled='disabled'  @click="handleToVerify" native-type="button"  type="primary">{{verifyInfo}}</van-button>
            </template>
            </van-field>
            <van-field native-type="submit" v-model="verify" type="digit" name="verify" label="验证码" :rules="[{ required: true, message: '请填写验证码' }]"/>
        <div style="margin: 16px;">
            <van-button round block type="info" native-type="submit">注册</van-button>
        </div>
        </van-form>
        <div class="login_link">
            <router-link to="/mine/login" >立即登录</router-link>
            <router-link to="/mine/findPassword" >找回密码</router-link>
        </div>
    </div>
</template>

<script>
import { Dialog } from 'vant';
export default {
    name:'register',
    data() {
    return {
      username: '',
      password: '',
      email:'',
      verify:'',
      verifyInfo:'发送验证码',
      disabled:false

      
    };
  },
  components: {
    [Dialog.Component.name]: Dialog.Component,
  },
  methods: {

        nameVal(val){
            return /^[a-zA-Z]\w{4,15}$/.test(val);
        },
        passwordVal(val){
            return /^\S*(?=\S{6,})(?=\S*\d)(?=\S*[A-Z])(?=\S*[a-z])(?=\S*[!@#$%^&*? ])\S*$/.test(val);
        },
        mailVal(val){
            return /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(val);
        },

        handleToVerify(){
            if (this.disabled) { return}
            this.axios({
                url:'/api2/users/verify?email=' + this.email
            }).then((res)=>{
                var status = res.data.status;
                var This = this
                if ( status === 0) {
                    Dialog.alert({
                    closeOnClickOverlay:true,
                    message: '验证码发送成功',
                    }).then(() => {
                    // on close
                    This.countDown()
                    });
                }else{
                    Dialog.alert({
                    closeOnClickOverlay:true,
                    message: '验证码发送失败',
                    })
                }
            })
        },


    onSubmit(values) {
      console.log('submit', values);
      this.axios({
            method: 'post',
            url:'/api2/users/register',
            data:{
                username : this.username,
                password : this.password,
                email:this.email,
                verify:this.verify
            }
      }).then((res)=>{
          var status = res.data.status;
          var This = this
          console.log(status);
          console.log(res);
          if (status === 0) {
                Dialog.alert({
                closeOnClickOverlay:true,
                message: '注册成功',
                }).then(() => {
                // on close
                This.$router.push('/mine/login')
                });
          }else{
                Dialog.alert({
                closeOnClickOverlay:true,
                message: res.data.msg + ',请重新注册',
                })
          }
      })


    },
    countDown(){
        this.disabled = true;
        var count = 60
        var timer = setInterval(() => {
            count--
            this.verifyInfo = '剩余' + count + '秒'
            if (count === 0) {
                this.disabled = false
                count = 60;
                this.verifyInfo = '发送验证码'
                clearInterval(timer)
            }
        }, 1000);
        console.log(timer);
    }
  
},
   

}
</script>

<style scoped>
.login_link{ display: flex; justify-content:space-between;}
.login_link a{ text-decoration: none; margin:0 5px; font-size: 12px; color:#e54847;}
</style>